header{display:flex;justify-content:space-around;align-items:center;width:100%;height:100px;background:lightsteelblue}header .logo-container{display:flex;align-items:center}header .logo{width:15%;height:100%}header .h.links{display:flex;text-decoration:none;list-style:none;justify-content:space-between;width:150px}header .h.links li{transition:0.2s}header .h.links li:hover{transform:scale(1.25)}header .h.links li .fab,header .h.links li .far{text-decoration:none;color:darkslateblue}
