.modal{display:block;position:fixed;z-index:1;padding:100px 0 0 0;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,0.3)}.modal-cntr{position:relative;background-color:#fefefe;margin:auto;padding:0;width:60%;max-width:850px;box-shadow:0px 4px 10px 0px rgba(0,0,0,0.2);-webkit-animation-name:animatetop;-webkit-animation-duration:0.4s;animation-name:animatetop;animation-duration:0.4s}@-webkit-keyframes animatetop{from{top:-300px;opacity:0}to{top:0;opacity:1}}@keyframes animatetop{from{top:-300px;opacity:0}to{top:0;opacity:1}}.modal-close{color:black;float:right;font-size:28px;font-weight:bold}.modal-close:hover,.modal-close:focus{text-decoration:none;cursor:pointer}.modal-header,.modal-footer{background-color:wheat;color:black;padding:2px 16px}.modal-header h2,.modal-footer h2{font-size:30px;text-align:center}.modal-body{padding:2px 16px;text-align:center}
